import { Button, Stack, Tab, Tabs } from "@mui/material"
import { GridCellParams } from "@mui/x-data-grid"
import React, { useState } from "react"
import { Link } from "react-router-dom"

import { Cover } from "@components"
import { sensorPageTabs } from "./_constants"
import { EchoSensors } from "./fragments"

/**
 * Страница датчиков
 *
 * @returns {React.FC} - FC
 */
const SensorsPage: React.FC = () => {
  const [activeTab, setActiveTab] = useState<string>(sensorPageTabs[0].value)
  const [isOpenModal, setIsOpenModal] = useState<boolean>(false)
  const [listSelectedSensors, setListSelectedSensors] = useState<string[]>([])
  const listSelectedSensorsLength = listSelectedSensors.length

  function eventSelectingSensors({ row }: GridCellParams): void {
    const rowName = row.name

    if (rowName) {
      setListSelectedSensors((prevState) => {
        prevState = [...prevState]

        if (!listSelectedSensors.includes(rowName)) {
          prevState.push(rowName)
        } else {
          return prevState.filter((prevRowName) => prevRowName !== rowName)
        }

        return prevState
      })
    }
  }

  function eventRemovingSensors(): void {
    setListSelectedSensors([])
  }

  function handleOpenModal(): void {
    setIsOpenModal(!isOpenModal)
  }

  return (
    <Cover
      title="Журнал подключенных датчиков"
      showBorderTitle={true}
      footerChildren={
        <Stack direction="row" spacing={2}>
          <Link to="/sensors/adding">
            <Button variant="contained">Добавить</Button>
          </Link>
          <Button
            disabled={!(listSelectedSensorsLength === 1)}
            variant="outlined"
          >
            Редактировать
          </Button>
          <Button
            disabled={!listSelectedSensorsLength}
            variant="outlined"
            onClick={(): void => setIsOpenModal(!isOpenModal)}
          >
            Удалить
          </Button>
        </Stack>
      }
    >
      <Tabs
        value={activeTab}
        variant="fullWidth"
        onChange={(event, newTab: string): void => setActiveTab(newTab)}
      >
        {sensorPageTabs.map(({ value, label, disabled }) => (
          <Tab value={value} label={label} key={value} disabled={disabled} />
        ))}
      </Tabs>
      <EchoSensors
        eventSelectingSensors={eventSelectingSensors}
        eventRemovingSensors={eventRemovingSensors}
        listSelectedSensors={listSelectedSensors}
        handleOpenModal={handleOpenModal}
        isOpenModal={isOpenModal}
      />
    </Cover>
  )
}

export default SensorsPage
