import { Button, Stack } from "@mui/material"
import React from "react"

import { Cover, DataTable } from "@components"
import {
  exampleModesTable,
  headerTableModes,
  exampleEquipment,
} from "./_constants"
import { Filter } from "./fragments"

/**
 * Страница Режимы
 *
 * @returns {React.FC} - FC
 */
const ModesPage: React.FC = () => (
  <Cover
    title="Режимы"
    footerChildren={
      <Stack direction="row" spacing={2}>
        <Button variant="contained">Добавить</Button>
        <Button disabled variant="outlined">
          Редактировать
        </Button>
        <Button disabled variant="outlined">
          Удалить
        </Button>
      </Stack>
    }
  >
    <Filter equipmentsList={exampleEquipment} />
    <DataTable rows={exampleModesTable} columns={headerTableModes} />
  </Cover>
)

export default ModesPage
