import { Button, Stack, styled } from "@mui/material"
import React from "react"

import { BasicDateRangePicker, Cover, DataTable, SearchForm } from "@components"
import { exampleRowsTableModeLog, columnsEventTable } from "./_constants"

/**
 * Фрагмент с таблицей "Журнал событий по датчику"
 *
 * @returns {React.FC} - FC
 */
export const ModeLog: React.FC = () => {
  return (
    <Cover
      title="Журнал событий по датчику"
      heightInherit="heightInherit"
      footerChildren={
        <Button variant="outlined" sx={{ mb: "20px" }} disabled>
          Удалить
        </Button>
      }
    >
      <Stack direction="row" spacing={2}>
        <SSearchForm />
        <BasicDateRangePicker />
      </Stack>
      <DataTable
        rows={exampleRowsTableModeLog}
        columns={columnsEventTable}
        height={420}
      />
    </Cover>
  )
}

const SSearchForm = styled(SearchForm)({
  marginLeft: "15px",
})
