import { Stack, styled, TextField, Typography } from "@mui/material"
import React, { ReactNode } from "react"

interface ITextField {
  title: string
  label: string
  variant?: "standard" | "filled" | "outlined"
  type?: "password" | "text"
  id: string
  childrenStack?: ReactNode
}

/**
 * Локальный компонент TextField с заголовком
 *
 * @param {string} title - Загловок поля ввода (слева)
 * @param {string} label - Загловок над полем ввода
 * @param {string} variant - Стиль поля ввода
 * @param {string} type - Тип поля ввода
 * @param {string} id - Уникальный ID, для поля ввода
 * @param {ReactNode} childrenStack - Дополнительное содержимое, для контейнера поля ввода
 * @param {object} otherProps - Любые другие значения
 * @returns {React.FC<ITextField>} - FC
 */
export const BaseTextField: React.FC<ITextField> = ({
  title,
  label,
  variant = "outlined",
  type = "text",
  id,
  childrenStack,
  ...otherProps
}) => (
  <Stack direction="row" spacing={3} alignItems="center" {...otherProps}>
    <Typography variant="body1" children={title} />
    <STextField id={id} label={label} variant={variant} type={type} />
    {childrenStack}
  </Stack>
)

const STextField = styled(TextField)({
  width: "360px",
})
