import { Button, Stack } from "@mui/material"
import React from "react"

import { Cover, DataTable } from "@components"
import { Filter } from "./fragments"
import {
  exampleTableEchoRecords,
  headerEchoRecordsTable,
  exampleEquipments,
} from "./_constants"

/**
 * Страница "Записи Echo"
 *
 * @returns {React.FC} - FC
 */
const EchoRecordsPage: React.FC = () => (
  <Cover
    title="Записи датчиков"
    footerChildren={
      <Stack direction="row" spacing={2}>
        <Button variant="contained">Добавить</Button>
        <Button disabled variant="outlined">
          Редактировать
        </Button>
        <Button disabled variant="outlined">
          Удалить
        </Button>
      </Stack>
    }
  >
    <Filter equipmentsList={exampleEquipments} />
    <DataTable
      rows={exampleTableEchoRecords}
      columns={headerEchoRecordsTable}
    />
  </Cover>
)

export default EchoRecordsPage
