import React, { Fragment } from "react"

import { Breadcrumbs } from "@components"
import { exampleParameters } from "./_constants"
import { ModuleDetails, ModeLog, ConnectedSensors } from "./fragments"

/**
 * Страница "отдельного оборудования"
 *
 * @returns {React.FC} - FC
 */
const EquipmentPage: React.FC = () => {
  const breadcrumbs = ["Журнал оборудований", "Сетевой насос СН-1"]

  return (
    <Fragment>
      <Breadcrumbs breadcrumbs={breadcrumbs} />
      <ModuleDetails
        title="Сетевой насос СН-1"
        parameters={exampleParameters}
      />
      <ModeLog />
      <ConnectedSensors />
    </Fragment>
  )
}

export default EquipmentPage
