import { Button } from "@mui/material"
import React from "react"

import { BasicDateRangePicker, Cover, DataTable } from "@components"
import { exampleRowsTableEvent, columnsEventTable } from "./_constants"

/**
 * Фрагмент с таблицей журнала по датчикам
 *
 * @returns {React.FC} - FC
 */
export const EventTable: React.FC = () => {
  return (
    <Cover
      title="Журнал событий по датчику"
      heightInherit="heightInherit"
      footerChildren={
        <Button variant="outlined" sx={{ mb: "20px" }} disabled>
          Удалить
        </Button>
      }
    >
      <BasicDateRangePicker />
      <DataTable
        rows={exampleRowsTableEvent}
        columns={columnsEventTable}
        height={420}
      />
    </Cover>
  )
}
