import React, { Fragment } from "react"

import { SPaper, SHeader, STypography, SFooter } from "./_styles"

interface ICover {
  title: string
  children: React.ReactNode
  headerChildren?: React.ReactNode
  footerChildren?: React.ReactNode
  showBorderTitle?: boolean
  heightInherit?: "heightInherit"
}

/**
 * Обертка компонентов страниц
 *
 * @param {string} title - Заголовок
 * @param {React.ReactNode} children - Дочерний элемент
 * @param {React.ReactNode} headerChildren - Дочерний элемент для хедера
 * @param {boolean} isBorderedTitle - Для стилизации состояния: с бордером/без бордера
 * @param {React.ReactNode} footerChildren - Дочерний элемент для футера
 * @param {string} heightInherit - Для растягивания обертки на всю высоту экрана
 * @returns {React.FC<ICover>} - FC
 */
export const Cover: React.FC<ICover> = ({
  title,
  children,
  headerChildren = null,
  showBorderTitle = false,
  footerChildren = null,
  heightInherit,
}) => (
  <Fragment>
    <SPaper variant={heightInherit}>
      <SHeader
        sx={{
          borderBottomColor: showBorderTitle
            ? "transparent"
            : (theme): string => theme.palette.primary.main,
        }}
      >
        <STypography variant="h6">{title}</STypography>
        {headerChildren}
      </SHeader>
      {children}
    </SPaper>
    {footerChildren && <SFooter>{footerChildren}</SFooter>}
  </Fragment>
)
