import { ListItem, Toolbar } from "@mui/material"
import React from "react"
import { NavLink, useLocation } from "react-router-dom"

import { itemNavigation } from "./_constants"
import { SDrawer, SList, SListItemText } from "./_styles"

/**
 * Фрагмент боковой панели навигации
 *
 * @returns {React.FC} - FC
 */
export const Sidebar: React.FC = () => {
  const { pathname } = useLocation()

  return (
    <SDrawer variant="permanent">
      <Toolbar />
      <SList>
        {itemNavigation.map(({ path, label, icon }) => (
          <NavLink
            to={path}
            key={label}
            className={({ isActive }): string =>
              isActive ? "nav_link_active" : ""
            }
          >
            <ListItem button sx={{ borderRight: "2px solid transparent" }}>
              <SListItemText>
                {icon(pathname.startsWith(path))}
                {label}
              </SListItemText>
            </ListItem>
          </NavLink>
        ))}
      </SList>
    </SDrawer>
  )
}
