import { Drawer, List, ListItemText, styled } from "@mui/material"

const SDrawer = styled(Drawer)({
  "width": "180px",
  "flexShrink": 0,

  "& .MuiDrawer-paper": {
    width: "180px",
    boxSizing: "border-box",
    background: "rgba(255, 255, 255, 0.01)",
  },
})

const SList = styled(List)(({ theme }) => ({
  "paddingTop": "20px",

  "a": {
    textDecoration: "none",
  },

  "& .nav_link_active": {
    "background":
      "linear-gradient(90deg, rgba(255, 255, 255, 0.1) -47.13%, rgba(255, 255, 255, 0.022) 200%)",
    "display": "block",

    "& span": {
      color: `${theme.palette.text.primary} !important`,
    },

    "& div": {
      borderRightColor: `${theme.palette.primary.main} !important`,
    },
  },
}))

const SListItemText = styled(ListItemText)(({ theme }) => ({
  span: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    textTransform: "uppercase",
    fontWeight: 500,
    fontSize: "14px",
    lineHeight: "24px",
    color: `${theme.palette.grey[700]}`,

    svg: {
      marginBottom: "6px",
    },
  },
}))

export { SDrawer, SList, SListItemText }
