import Typography from "@mui/material/Typography"
import { GridCellParams } from "@mui/x-data-grid"
import React, { Fragment } from "react"

import { BasicModal, DataTable } from "@components"
import { Filter } from "./_Filter"
import {
  exampleSensorTable,
  headerTableSensors,
  exampleEquipments,
  exampleConnectionStatus,
  exampleRecordsStatus,
} from "./_constants"

interface IEchoSensors {
  eventSelectingSensors: (params: GridCellParams) => void
  eventRemovingSensors: () => void
  listSelectedSensors: string[]
  handleOpenModal: () => void
  isOpenModal: boolean
}

/**
 * Содержимое вкладки "Сенсоры Echo"
 *
 * @param {() => void} eventRemovingSensors - Событие удаления сенсора
 * @param {string[]} listSelectedSensors - Список выбранных сенсоров
 * @param {() => void} handleOpenModal - Событие открытия всплывающего окна
 * @param {boolean} isOpenModal - Статуст открыто всплывающее окно или нет
 * @returns {React.FC<IEchoSensors>} - FC
 */
export const EchoSensors: React.FC<IEchoSensors> = ({
  eventRemovingSensors,
  listSelectedSensors,
  handleOpenModal,
  isOpenModal,
}) => {
  return (
    <Fragment>
      <Filter
        equipmentsList={exampleEquipments}
        connectionStatusList={exampleConnectionStatus}
        recordsStatusList={exampleRecordsStatus}
      />
      <DataTable rows={exampleSensorTable} columns={headerTableSensors} />
      <BasicModal
        handleButtonSuccess={eventRemovingSensors}
        handleOpenModal={handleOpenModal}
        buttonSuccessName="удалить"
        isOpenModal={isOpenModal}
        title="Удалить датчик"
      >
        <Typography
          variant="body2"
          display="block"
          gutterBottom
          sx={{ mb: "20px" }}
        >
          Вы уверены, что хотите удалить датчик с устройства? <br />
          Все данные будут удалены без возможности восстановления.
        </Typography>
        {listSelectedSensors.map((row) => (
          <Typography variant="body2" display="block">
            {row}
          </Typography>
        ))}
      </BasicModal>
    </Fragment>
  )
}
