import { PowerSettingsNew } from "@mui/icons-material"
import { Button, Stack, Typography } from "@mui/material"
import React, { useEffect, useState } from "react"
import { Link } from "react-router-dom"

import logo from "@assets/images/logo.svg"
import { SAppBar, SToolbar } from "./_styles"

/**
 * Фрагмент верхней панели с датой, временем и кнопкой логина
 *
 * @returns {React.FC} - FC
 */
export const HeaderBar: React.FC = () => {
  const [date, setDate] = useState<Date | string>(
    new Date().toLocaleDateString()
  )
  const [time, setTime] = useState<Date | string>(
    new Date().toLocaleTimeString()
  )

  useEffect(() => {
    const secTimer = setInterval((): void => {
      setTime(new Date().toLocaleTimeString())
      setDate(new Date().toLocaleDateString())
    }, 1000)

    return () => clearInterval(secTimer)
  }, [])

  return (
    <SAppBar position="static">
      <SToolbar>
        <Link to="/">
          <img src={logo} alt="Echo" />
        </Link>
        <Stack spacing={4} direction="row" alignItems="center">
          <Typography variant="h5" component="div">
            {time}
          </Typography>
          <Typography variant="h5" component="div">
            {date}
          </Typography>
          <Button
            variant="text"
            startIcon={<PowerSettingsNew />}
            color="inherit"
            size="large"
          >
            Пользователь
          </Button>
        </Stack>
      </SToolbar>
    </SAppBar>
  )
}
