import { CssBaseline, ThemeProvider } from "@mui/material"
import React from "react"
import { HashRouter, Navigate, Route, Routes } from "react-router-dom"

import { PageWrapper } from "@components"
import { theme } from "./constants"
import {
  LoginPage,
  NotFoundPage,
  SensorsPage,
  EchoRecordsPage,
  EquipmentsPage,
  ModesPage,
  SensorPage,
  EquipmentPage,
  AddingSensor,
} from "./pages"

/**
 * @returns {React.FC} - FC
 */
const App: React.FC = () => (
  <HashRouter>
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Routes>
        <Route path="/" element={<PageWrapper />}>
          <Route index element={<Navigate to="/sensors" replace />} />
          <Route path="/sensors" element={<SensorsPage />} />
          <Route path="/sensors/:id" element={<SensorPage />} />
          <Route path="/sensors/adding" element={<AddingSensor />} />
          <Route path="/echo-records" element={<EchoRecordsPage />} />
          <Route path="/equipment" element={<EquipmentsPage />} />
          <Route path="/equipment/:id" element={<EquipmentPage />} />
          <Route path="/modes" element={<ModesPage />} />
        </Route>
        <Route path="/login" element={<LoginPage />} />
        <Route path="*" element={<NotFoundPage />} />
      </Routes>
    </ThemeProvider>
  </HashRouter>
)

export default App
