import { Button, Stack, Tab, Tabs } from "@mui/material"
import React, { useState } from "react"

import { Cover } from "@components"
import { connectionSensorTabs } from "./_constants"
import { EchoSensors } from "./tabs"

/**
 * Фрагмент с таблицей "Журнал событий по датчику"
 *
 * @returns {React.FC} - FC
 */
export const ConnectedSensors: React.FC = () => {
  const [activeTab, setActiveTab] = useState<string>(
    connectionSensorTabs[0].value
  )

  return (
    <Cover
      heightInherit="heightInherit"
      title="Журнал подключенных датчиков"
      showBorderTitle={true}
      footerChildren={
        <Stack direction="row" spacing={2} sx={{ mb: "20px" }}>
          <Button variant="contained">Добавить</Button>
          <Button disabled variant="outlined">
            Редактировать
          </Button>
          <Button disabled variant="outlined">
            Удалить
          </Button>
        </Stack>
      }
    >
      <Tabs
        value={activeTab}
        variant="fullWidth"
        onChange={(event, newTab: string): void => setActiveTab(newTab)}
      >
        {connectionSensorTabs.map(({ value, label, disabled }) => (
          <Tab value={value} label={label} key={value} disabled={disabled} />
        ))}
      </Tabs>
      <EchoSensors />
    </Cover>
  )
}
