import { Alert, Switch } from "@mui/material"
import { GridColDef, GridRenderCellParams } from "@mui/x-data-grid"
import React from "react"
import { Link } from "react-router-dom"

import { ISelectMenuItemsList } from "@types"

/**
 * Данные для заголовков ячеек таблицы "Сенсоры ECHO"
 */
const headerTableSensors: GridColDef[] = [
  { field: "id", hide: true, type: "number" },
  {
    field: "name",
    headerName: "Имя",
    type: "string",
    flex: 0.001,
    renderCell: (params): JSX.Element => (
      <Link to={`/sensors/${params.row.id}`}>{params.row.name}</Link>
    ),
  },
  {
    field: "ip",
    headerName: "IP адрес (идентификатор)",
    type: "string",
    flex: 0.001,
  },
  {
    field: "equipment",
    headerName: "Оборудование",
    type: "string",
    flex: 0.001,
  },
  {
    field: "status",
    headerName: "Статус подключения",
    type: "boolean",
    flex: 0.001,
    renderCell: (params): JSX.Element =>
      params.value ? (
        <Alert severity="success">В работе</Alert>
      ) : (
        <Alert severity="error">Отключен</Alert>
      ),
  },
  {
    field: "record",
    headerName: "Статус записи",
    type: "boolean",
    flex: 0.001,
    renderCell: (params: GridRenderCellParams<boolean>): JSX.Element => (
      <Switch color="success" defaultChecked={params.value} />
    ),
  },
]

interface IExampleSensorTable {
  id: number
  name: string
  ip: string
  equipment: string
  status: boolean
  record: boolean
}

