import { Button, Stack } from "@mui/material"
import React from "react"

import { Cover, DataTable } from "@components"
import {
  exampleTableEquipments,
  headerEquipmentsTable,
  exampleEquipments,
} from "./_constants"
import { Filter } from "./fragments"

/**
 * Страница Оборудование
 *
 * @returns {React.FC} - FC
 */
const EquipmentsPage: React.FC = () => (
  <Cover
    title="Журнал оборудования"
    footerChildren={
      <Stack direction="row" spacing={2}>
        <Button variant="contained">Добавить</Button>
        <Button disabled variant="outlined">
          Редактировать
        </Button>
        <Button disabled variant="outlined">
          Удалить
        </Button>
      </Stack>
    }
  >
    <Filter equipmentsList={exampleEquipments} />
    <DataTable rows={exampleTableEquipments} columns={headerEquipmentsTable} />
  </Cover>
)

export default EquipmentsPage
