import { Button } from "@mui/material"
import React from "react"

import { BasicDateRangePicker, Cover, DataTable } from "@components"
import { columnsRecordsTable, exampleRowsTableRecords } from "./_constants"

/**
 * Фрагмент с таблицей записей датчиков
 *
 * @returns {React.FC} - FC
 */
export const RecordsTable: React.FC = () => {
  return (
    <Cover
      title="Записи датчиков"
      heightInherit="heightInherit"
      footerChildren={
        <Button variant="outlined" sx={{ mb: "20px" }} disabled>
          Удалить
        </Button>
      }
    >
      <BasicDateRangePicker />
      <DataTable
        rows={exampleRowsTableRecords}
        columns={columnsRecordsTable}
        height={420}
      />
    </Cover>
  )
}
