import { DataGrid, GridColDef, GridRowModel } from "@mui/x-data-grid"
import React, { useState } from "react"

interface IDataTable {
  rows: GridRowModel[]
  columns: GridColDef[]
  checkboxSelection?: boolean
  height?: number | string
}

/**
 * Компонент DataGrid, для отрисовки таблиц
 *
 * @param {readonly} rows - Массив с данных
 * @param {GridColDef[]} columns - Заголовки ячеек таблицы
 * @param {boolean} checkboxSelection - Данные с чекбоксами / без чекбокса
 * @param {number} height - Задает высоту таблицы
 * @returns {React.FC<IDataTable>} - FC
 */
export const DataTable: React.FC<IDataTable> = ({
  rows,
  columns,
  checkboxSelection = true,
  height,
}) => {
  const [pageSize, setPageSize] = useState<number>(20)

  return (
    <div style={{ height: `${height}px` }}>
      <DataGrid
        rows={rows}
        columns={columns.map((column) => ({
          ...column,
          sortable: false,
        }))}
        autoHeight={!height}
        pageSize={pageSize}
        onPageSizeChange={(newPage): void => setPageSize(newPage)}
        rowsPerPageOptions={[20, 50, 100]}
        checkboxSelection={checkboxSelection}
        disableSelectionOnClick
        disableColumnFilter
        disableColumnSelector
      />
    </div>
  )
}
