import React, { Fragment } from "react"

import { DataTable } from "@components"
import { Filter } from "./_Filter"
import {
  exampleSensorTable,
  headerTableSensors,
  exampleEquipments,
  exampleConnectionStatus,
  exampleRecordsStatus,
} from "./_constants"

/**
 * Содержимое вкладки "Сенсоры Echo"
 *
 * @returns {React.FC} - FC
 */
export const EchoSensors: React.FC = () => {
  return (
    <Fragment>
      <Filter
        equipmentsList={exampleEquipments}
        connectionStatusList={exampleConnectionStatus}
        recordsStatusList={exampleRecordsStatus}
      />
      <DataTable
        height={420}
        rows={exampleSensorTable}
        columns={headerTableSensors}
      />
    </Fragment>
  )
}
