import { Box, Button, Stack } from "@mui/material"
import React from "react"

import { BaseTextField, Cover } from "@components"
import {
  SContent,
  STitleForm,
  SStatusConnection,
  SAlert,
  SBaseTextField,
} from "./_styles"

/**
 * Фрагмент "Подключение нового датчика"
 *
 * @returns {React.FC} - FC
 */
export const ConnectingNewSensor: React.FC = () => (
  <Cover title="Подключение нового датчика" heightInherit="heightInherit">
    <SContent>
      <Stack direction="row" justifyContent="space-between">
        <Stack>
          <STitleForm variant="body1" children="Подключение" />
          <BaseTextField
            title="Идентификатор"
            label="Идентификатор"
            id="id"
            childrenStack={
              <SStatusConnection>
                <Button variant="contained" disabled>
                  Подключение
                </Button>
                <SAlert severity="error">Нет подключения</SAlert>
              </SStatusConnection>
            }
          />
          <SBaseTextField
            title="Пароль"
            label="Пароль"
            id="password"
            type="password"
          />
        </Stack>
        <Box>
          <STitleForm variant="body1" children="Описание датчика" />
          <Stack direction="row" spacing={5}>
            <BaseTextField
              title="Название"
              label="Введите название"
              id="sensor-name"
            />
            <BaseTextField
              title="Описание"
              label="Введите описание"
              id="sensor-description"
            />
          </Stack>
        </Box>
      </Stack>
    </SContent>
  </Cover>
)
